<!-- 影院 -->
<template>
<section id="filmYard">
    <div class="topNav flex bot_bor">
        <div class="pro" @click="cityShow=true;regShow=false">{{proName}}<i>>></i></div>
        <div class="reg" @click="regShow=true">{{regName}}<i>>></i></div>
    </div>
    <v-pro v-show="cityShow" @close="close"></v-pro>
    <v-reg v-show="regShow" @regClose="regClose"></v-reg>
    <v-yard></v-yard>
</section>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import proCity from '@/components/filmYard_cityList/proCity'
import regCity from '@/components/filmYard_cityList/regCity'
import yardList from '@/components/filmYard_yardList/yardList'
export default {
//import引入的组件需要注入到对象中才能使用
name:"filmYard",
data(){
    return{
        cityShow:false,
        regShow:false,
        proName:'北京',
        proCode:'bj',
        regName:'全部区域',
    }
},
components:{
    "v-pro":proCity,
    "v-reg":regCity,
    "v-yard":yardList,
},
methods:{
    close(data){
        this.cityShow = data;
    },
    regClose(data){
        this.regShow = data;
    },
    getPro(){
        this.$root.Bus.$on('pro',data=>{
            this.proName=data[0];
            this.proCode=data[1];
            this.regName='全部区域'
        })
    },
    getReg(){
        this.$root.Bus.$on('regData',data=>{
            this.regName=data.name
        })
    }
},
created(){
    this.getPro();
    this.getReg();
}

}
</script>
<style scoped>
    #filmYard{padding-top:34px;}
    .topNav{
        position: fixed;
        top:0;
        width:100%;
        background-color: #eee;
        padding:4px 0;
    }
    .topNav>div{
        height:25px;
        line-height: 25px;
        flex:1;
        text-align: center;
        cursor:pointer;
    }
    .topNav .pro{
        border-right: 1px solid #ccc;
    }
</style>


