<!--  -->
<template>
    <section>
        <!-- 热门城市 -->
        <div class="filmNav bot_bor top_bor">
            <v-city class="hotCity" :cityName="cityName" @secCity="getMySecCity"></v-city>
            <div class="showFilm">
                <div class="t_center pointer" @click="filmId=0" :class="{'active' : filmId===0}">正在热映</div>
                <div class="t_center pointer" @click="filmId=1;" :class="{'active' : filmId===1}">即将热映</div>
                <div class="botLine" :class="{'moveRight' : filmId===1}"></div>
            </div>
        </div>
        <!-- 正在热映 -->
        <v-liveShow v-show="filmId==0" v-bind:cityArr="cityArr"></v-liveShow>
        <!-- 即将热映 -->
        <v-soonShow v-show="filmId==1" :cityArr="cityArr"></v-soonShow>
    </section>
</template>

<script>
import hotCity from "@/components/hotCity/hotCity"
import liveShow from "@/components/liveShow/liveShow"
import soonShow from "@/components/soonShow/soonShow"

export default {
    name:"films",
    data(){
        return{
            filmId:0,
            cityCode:'bj',
            cityName:'北京',
            cityArr:['北京','bj']
        }
    },
    components:{
        'v-city':hotCity,
        'v-liveShow':liveShow,
        'v-soonShow':soonShow
    },
    methods:{
        getMySecCity(arr){
            this.cityArr = arr
            // console.log(arr,"cityArr------------films.vue")
        }
    },
    created(){
        if(this.$route.params.cityCode){
            var code = this.$route.params.cityCode;
            var name = this.$route.params.cityName;
            var id = this.$route.params.filmId;
            this.cityCode = code;
            this.cityName = name;
            this.filmId = id;
            var arr = [name,code];
            this.cityArr = arr;
        }
        // console.log(this.$route.params,'接收的路由参数------films.vue')
    },
}
</script>
<style scoped>
    .filmNav{
        display: flex;
        line-height: 45px;
    }
    .filmNav .hotCity{
        flex:1;
        text-align: center;
    }
    .filmNav .showFilm{
        position: relative;
        flex:2;
        display: flex;
        z-index:1;
    }
    .filmNav .showFilm>div{
        flex:1;
        transition:left 0.5s;
    }
    .filmNav .showFilm div.active{
        color:red;
    }
    .filmNav .showFilm .botLine{
        width:50%;
        height: 2px;
        background-color: red;
        position: absolute;
        bottom:0;
        left:0;
    }
    .filmNav .showFilm .moveRight{
        left:50%;
    }
</style>


