<!-- 电影院列表 -->
<template>
<section id="yardList">
    <ul class="list p_l p_b50" v-show="noYard===false">
        <li class="bot_bor_c" v-for="(item,index) in yardData" :key="index">
            <h3 class="title mb8">{{item.name}}<span :style="{display:item.isTure==true?'inline-block':'none'}">4K厅</span></h3>
            <div class="local mb6">{{item.local}}</div>
            <div class="money">
                <span class="zuo">座</span>
                <span class="newUser">新人专享</span>
                <span class="num">￥{{item.money}}元起</span>
            </div>
        </li>
    </ul>
    <div class="noYard" v-show="noYard">
        <img src="http://hbimg.huabanimg.com/b209937dc2276a7cb5a47fb831879af25c52fa1b25567-NGX9jR_fw658" alt="">
    </div>
    
</section>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
//import引入的组件需要注入到对象中才能使用
name:'yardList',
components: {},
data() {
//这里存放数据
return {
    pro:'bj',
    reg:'all',
    yardData:[],
    noYard:false,
};
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//方法集合
methods: {
    getYardData(pro,reg){
        this.axios.get('yardList').then(res=>{
            if(res.data.result=="success"){
                var proData = res.data.data;
                for(let a in proData){
                    //循环省份
                    if(a==pro){
                        var regData = proData[a];
                        this.noYard=false;
                        if(reg=='all'){
                            //全部区域
                            var arr = [];
                            for(let i in regData){
                                var _arr = regData[i];
                                for(var j = 0;j<_arr.length;j++){
                                    arr.push(_arr[j])
                                }
                            }
                            this.noYard=false;
                            this.yardData = arr;
                        }else{
                            for(let b in regData){
                                //循环区域
                                if(b==reg){
                                this.noYard=false;
                                this.yardData = regData[b];
                                return;
                                }else{
                                    this.noYard=true;
                                }
                            }
                        }
                        
                        
                       
                    }
                }
            }
            
        }).catch(err=>{
            console.log(err)
        })
    },
    getData(){
        this.$root.Bus.$on('pro',data=>{
            this.getYardData(data[1],'all')
        })
        this.$root.Bus.$on('regData',data=>{
            this.getYardData(data.pro,data.reg)
        })
    }
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {
    this.getYardData('bj','all');
    this.getData();
},

}
</script>
<style scoped>
#yardList .list>li{
    padding: 15px 0 10px;
}
#yardList .list>li>h3>span{
    display: inline-block;
    border: 1px solid #ccc;
    color:#ccc;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    border-radius: 1px;
    margin-left: 5px;
}
#yardList .list .money>span{
    display: inline-block;
}
#yardList .list .money .zuo{
    line-height: 17px;
    color:#23B8FF;
    border:1px solid #23B8FF;
    border-radius: 2px;
}
#yardList .list .money .newUser{
    color:#fff;
    background-color:#f8ab06;
    line-height: 17px;
    border-radius: 2px;
}
#yardList .noYard{
    position: fixed;
    top:34px;
}
#yardList .noYard>img{
    width:100%;
}

</style>


