<!-- 即将热映板块 -->
<template>
<section id="soonShow">
    <ul class="filmList">
      <li class="bot_bor_c" v-for="(item,index) in filmData" :key="index">
        <div class="filmCon">
          <div class="flex">
            <div class="img"><img class="image" :src="item.imgUrl" alt=""></div>
            <div class="intro ellipsis" @click="sendFilmData(index)">
              <router-link class="filmLink" :to="{name:'filmDetail',params:{cityName:cityArr[0],cityCode:cityArr[1],filmId:1}}">
                <h3 class="ellipsis">{{item.name}}</h3>
                <div class="rank">{{item.rank}}人想看</div>
                <p
                  class="detail mt6 ellipsis"
                >导演：{{item.director}}</p>
                <p class="mt6 ellipsis">主演：{{item.actor}}</p>
              </router-link>
            </div>
          </div>
          <button class="buy">预售</button>
        </div>
      </li>
    </ul>
</section>
</template>

<script>

export default {
name:"soonShow",
props:["cityArr"],
components: {
},
data() {
return {
    filmData:[],
};
},
methods: {
    getFilmData(cityCode){
        this.axios.get("soonShow").then(res=>{
              if(res.data.result == "success"){
                  this.filmData = res.data.data[cityCode]
              }
          }).catch(err=>{
              console.log(err)
          })
    },
    sendFilmData(index){
        this.$root.Bus.$emit("filmIndex",this.filmData[index])
    }
},
created() {
    this.getFilmData(this.cityArr[1]);
},
watch:{
    cityArr(){
        this.getFilmData(this.cityArr[1]);
    }
}
}
</script>
<style scoped>
.filmList {
  padding-left: 10px;
  padding-bottom: 50px;
}
.filmList li{
    margin-top: 15px;
}
.filmList .filmCon{position: relative;}
.filmList li .img {
  width: 65px;
  height: 90px;
  border: 1px solid #ccc;
}
.filmList .intro {
  flex: 1;
  margin-left: 10px;
  padding-bottom: 15px;
}
.filmList .intro .filmLink .detail{
    width:75%;
}
.filmList .intro .filmLink .rank{
    color:#f8ab06;
}
.filmList li .filmCon button {
  position: absolute;
  right:20px;
  bottom:50px;
  color:#23B8FF;
  border-color:#23B8FF;
}
</style>


