<!-- 区域 -->
<template>
<section id="regCity" :style="{height:height}" @click.stop="close()">
    <ul class="regList">
        <li class="bot_bor_c" @click.stop="sendRegData(pro,item.code,item.name)" v-for="(item,index) in regList" :key="index">{{item.name}} ({{item.num}})</li>
    </ul>
</section>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
//import引入的组件需要注入到对象中才能使用
name:'regCity',
components: {},
data() {
//这里存放数据
return {
    height:null,
    regShow:false,
    regList:[],
    pro:'bj'
};
},
//方法集合
methods: {
    getWinHeight(){
        this.height = window.innerHeight - 34 + 'px';
    },
    close(){
        this.$emit("regClose",this.regShow)
    },
    getRegData(proName){
        this.axios.get("hotCity").then(res=>{
            if(res.data.result == 'success'){
                this.regList=res.data.data['bj'];
                this.$root.Bus.$on('pro',data=>{
                    this.regList=res.data.data[data[1]];
                    this.pro=data[1];
                })
            }
            
        }).catch(err=>{
            console.log(err)
        })
    },
    sendRegData(pro,reg,name){
        this.$root.Bus.$emit('regData',{pro,reg,name})
        this.close();
    }
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {
    this.getWinHeight();
    this.getRegData();
},
beforeCreate(){
    
}
}
</script>
<style scoped>
#regCity{
    position: fixed;
    top:34px;
    left:0;
    width:100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: 999999;
}
#regCity .regList{
    background-color: #fff;
    padding:0 10px;
    line-height: 45px;
}
</style>


