<!-- 省份城市列表 -->
<template>
  <section class="proCity">
    <div class="title bot_bor t_center">选择热门<span class="pospannter" @click="close()">x</span></div>
    <div class="cityDetails">
        <div class="citys rig_bor" ref="cityScroll">
            <ul>
                <li>
                <span id="hot" ref="hotDiv">热门</span>
                <div @click="getCity(item)" v-for="(item,index) in hotCity" :key="index">{{item}}</div>
                </li>
                <li v-for="(item,props) in cityList" :key="props" :ref="props">
                <span :id="props">{{props}}</span>
                <div @click="getCity(item.cityName)" v-for="(item,index) in item" :key="index">{{item.cityName}}</div>
                </li>
            </ul>
        </div>

        <ul class="cityCode t_center">
            <li class="pointer" @click="scrollToHot">热门</li>
            <li class="pointer" @click="scrollTo(props)" v-for="(item,props) in cityList" :key="props">{{props}}</li>
        </ul>
    </div>
    
  </section>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import BScroll from "better-scroll";
export default {
  name: "proCity",
  data() {
    return {
        cityShow:false,
      cityList: [],
      hotCity:["北京","上海","广州"],
      cityCode:'bj',
    };
  },
  methods:{
    getCity(cityName){
      this.cityCode = this.match(cityName);
      //传值
      var cityArr = [];
      cityArr.push(cityName,this.cityCode);
      this.$root.Bus.$emit("pro",cityArr) //向其他组件发送值
    //   this.$emit('myPro',cityArr)  //向父组件hotCity发送值
      this.close();
    },
    //匹配，为其他非热门城市随机匹配数据
    match(cityName){
      var arr = ['bj','sh','gz'];
      var randomCode = arr[Math.floor(Math.random()*arr.length)];
      switch(cityName){
        case "北京":
          return "bj";
          break;
        case "上海":
          return "sh";
          break;
        case "广州":
          return "gz";
          break;
      }
      return randomCode;
      
    },
    //关闭
    close(){
        this.$emit('close',this.cityShow);
    },
    scrollToHot(){
      const hotElement=this.$refs.hotDiv;
      this.scroll.scrollToElement(hotElement)
    },
    scrollTo(props){
      const element=this.$refs[props][0];
      this.scroll.scrollToElement(element)

    }
  },
  created() {
    //获取城市数据
    this.axios
      .get("cityList")
      .then(res => {
        this.cityList = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
   
  },
  mounted() {
    //定时器
    this.$nextTick(() => {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.cityScroll, {
          click: true
        });
      } else if (!this.$refs.cityScroll) {
        return;
      } else {
        this.scroll.refresh();
      }
    });
  },
  
};
</script>
<style scoped>
.proCity{
    line-height: 45px;
    position: fixed;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background-color: #fff;
    z-index:100;
}
.proCity .cityDetails{
  display: flex;
  height: 100%;
}
.proCity .title{
    position: relative;
    font-size: 22px;
    font-weight: bold;
    width:100%;
}
.proCity .title span{
    position: absolute;
    right:20px;
}
.proCity .citys {
  flex: 1;
  text-align: left;
  padding-right: 30px;
  overflow: hidden;
}
.proCity .citys >ul{
    padding-bottom: 80px;
}
.proCity .citys >ul> li > span {
  display: block;
  background-color: #e9e9e9;
  font-size: 20px;
  font-weight: bold;
  padding-left: 20px;
}
.proCity .citys>ul > li > div {
  padding-left: 20px;
  cursor: pointer;
}
.proCity .cityCode {
  width: 50px;
  line-height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
</style>


